import { Component, Inject } from 'vue-property-decorator';

import { mixins } from 'vue-class-component';
import JhiDataUtils from '@/shared/data/data-utils.service';

import { required } from 'vuelidate/lib/validators';

import MissionService from '@/entities/mission/mission.service';
import { IMission } from '@/shared/model/mission.model';

import { ISpaceEvent, SpaceEvent } from '@/shared/model/space-event.model';
import SpaceEventService from './space-event.service';

const validations: any = {
  spaceEvent: {
    name: {
      required,
    },
    date: {
      required,
    },
    description: {
      required,
    },
    photo: {
      required,
    },
    type: {
      required,
    },
  },
};

@Component({
  validations,
})
export default class SpaceEventUpdate extends mixins(JhiDataUtils) {
  @Inject('spaceEventService') private spaceEventService: () => SpaceEventService;
  public spaceEvent: ISpaceEvent = new SpaceEvent();

  @Inject('missionService') private missionService: () => MissionService;

  public missions: IMission[] = [];
  public isSaving = false;
  public currentLanguage = '';

  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (to.params.spaceEventId) {
        vm.retrieveSpaceEvent(to.params.spaceEventId);
      }
      vm.initRelationships();
    });
  }

  created(): void {
    this.currentLanguage = this.$store.getters.currentLanguage;
    this.$store.watch(
      () => this.$store.getters.currentLanguage,
      () => {
        this.currentLanguage = this.$store.getters.currentLanguage;
      }
    );
  }

  public save(): void {
    this.isSaving = true;
    if (this.spaceEvent.id) {
      this.spaceEventService()
        .update(this.spaceEvent)
        .then(param => {
          this.isSaving = false;
          this.$router.go(-1);
          const message = this.$t('space1App.spaceEvent.updated', { param: param.id });
          return this.$root.$bvToast.toast(message.toString(), {
            toaster: 'b-toaster-top-center',
            title: 'Info',
            variant: 'info',
            solid: true,
            autoHideDelay: 5000,
          });
        });
    } else {
      this.spaceEventService()
        .create(this.spaceEvent)
        .then(param => {
          this.isSaving = false;
          this.$router.go(-1);
          const message = this.$t('space1App.spaceEvent.created', { param: param.id });
          this.$root.$bvToast.toast(message.toString(), {
            toaster: 'b-toaster-top-center',
            title: 'Success',
            variant: 'success',
            solid: true,
            autoHideDelay: 5000,
          });
        });
    }
  }

  public retrieveSpaceEvent(spaceEventId): void {
    this.spaceEventService()
      .find(spaceEventId)
      .then(res => {
        this.spaceEvent = res;
      });
  }

  public previousState(): void {
    this.$router.go(-1);
  }

  public clearInputImage(field, fieldContentType, idInput): void {
    if (this.spaceEvent && field && fieldContentType) {
      if (Object.prototype.hasOwnProperty.call(this.spaceEvent, field)) {
        this.spaceEvent[field] = null;
      }
      if (Object.prototype.hasOwnProperty.call(this.spaceEvent, fieldContentType)) {
        this.spaceEvent[fieldContentType] = null;
      }
      if (idInput) {
        (<any>this).$refs[idInput] = null;
      }
    }
  }

  public initRelationships(): void {
    this.missionService()
      .retrieve()
      .then(res => {
        this.missions = res.data;
      });
  }
}
